:host {
    display: block;
}

.container-fluid {
    max-width: 1400px;
    margin: 0 auto;
}

:host ::ng-deep {
    .p-card {
        margin-bottom: 1rem;

        .p-card-header {
            padding: 0;
        }

        .p-card-body {
            padding: 1rem;
        }
    }

    .p-datatable {
        .p-datatable-thead > tr > th {
            background: var(--surface-100);
            font-weight: 600;
        }

        .p-datatable-tbody > tr > td {
            padding: 0.75rem 1rem;
        }
    }
}
